
.buttonlink_container{
    align-items: center;
    display: flex;
    padding: 5px 10px;
    border-radius: 5px;
    border: none;
    justify-content: start;
    text-align: start;

}
.buttonlink:hover{
    cursor: pointer;
}
.buttonlink_image{
    width:20%;
    margin-right:10px;
    fill: white;

}
@media screen and (max-width:600px) {
    .buttonlink{
        margin: 10px;
    }
    .buttonlink_image{
        width:20%;
        margin-right:10px;
    }
    .buttonlink_container{
        align-items: center;
        display: flex;
        justify-content: center;
        padding: 5px 10px;
        border-radius: 5px;
        border: none;
    
    }
}